*{
    margin: 0%;
    padding: 0%;
}
.home_body{
    margin-top: 51px;
    display: flex;
    flex-direction: column;
    width: 100%;

}
.home_slider{
    width: 100%;
    height: 400px;
    background-size: 100% 400px;
    background-repeat: no-repeat;
    background-image: url("https://www.apple.com/v/home/ay/images/promos/apple-watch-series-8/promo_apple_watch_series_8__ch7rexplmihe_large.jpg");
    animation: slider 20s infinite linear;
}
@keyframes slider{
    0%{
        background-image: url("https://www.apple.com/v/home/ay/images/promos/apple-watch-series-8/promo_apple_watch_series_8__ch7rexplmihe_large.jpg");    }
    25%{
        background-image: url("https://www.apple.com/v/home/ay/images/promos/macbook-pro-14-and-16/promo_mbp_preorder__faqgyfz56umq_large.jpg");
    }
    50%{
        background-image: url("https://www.teahub.io/photos/full/89-896761_apple-laptop-black-background.jpg");
    }
    75%{
        background-image: url("https://www.deccanherald.com/sites/dh/files/articleimages/2022/09/08/apple-iphone-14-pro-series-1143382-1662642675.jpg");
    }
    100%{
        background-image: url("https://images.hdqwalls.com/wallpapers/maserati-watches-pic.jpg");
    }
}
.home_information{
    height: 400px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    background-color: #e8eaf6;
}
.hove_left_container_information{
    height: 300px;
    width: 400px;
    display: flex;
    align-items: center;
    
}
.hove_right_container_information{
    height: 400px;
    width: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.home_img{
    box-shadow: 0 0 10px black inset;    
}
.home_intro{
    font-size: 40px;
    color: darkblue;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.home_intro::after{
    content: "\A A-z Cartz";
    white-space: pre;
    tab-size: 5;
    color: darkmagenta;
    font-size: 80px;
    padding-left: 150px;
}
.home_img{
    background: transparent;
}
.home_last_section{
    height: 400px;
    width: 100%;
    background-color: #f0f4c3;
}
.home_item_images1,.home_item_images2,.home_item_images3,.home_item_images4{
    height: 200px;
    width: 300px;
    border-radius: 10px;
    transition: box-shadow .3s linear .2s;
}
.home_item_images1:hover,.home_item_images2:hover,.home_item_images3:hover,.home_item_images4:hover{
  
    box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
    
}
.home_item_images1{
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-image: url("https://assets.adidas.com/images/w_383,h_383,f_auto,q_auto,fl_lossy,c_fill,g_auto/005a8d58fceb4f9985a7adaa007be9ce_9366/big-logo-fleece-hoodie.jpg");
}
.home_item_images2{
    background-size: 100% 100%;
    background-image: url("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTmZMqE4tu-t1FiDdXPhpcR5vmvChWm4jcCbQ&usqp=CAU");
}
.home_item_images3{
    background-size: 100% 100%;
    background-image: url("data:image/jpeg;base64,");
}
.home_item_images4{
    background-size: 100% 100%;
    background-image: url("https://www.shutterstock.com/image-vector/best-shopping-tour-design-template-260nw-1704993184.jpg");
}
.home_last_section{
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.home_last_section_btn{
    height: fit-content;
    width: fit-content;
    background: transparent;
    border: none;
}